export default {
	data() {
		return {
			isDisplay: false,
			selfTab: [],
			residueTab: [],
			loading: true

		}
	},
	onLoad() {
		this.getTabList()
	},
	methods: {
		displayClear() {
			this.isDisplay = !this.isDisplay
			if (!this.isDisplay) {
				this.putTabList(this.selfTab)
				// 更新首页的状态栏
				// uni.$emit('refreshIndexPage')
			}
		},
		getTabList() {
			this.$api.getTabListC({
				type: 'all'
			}).then(res => {
				this.loading = false
				const {
					data
				} = res.result
				this.selfTab = data.filter(item => item.self)
				this.residueTab = data.filter(item => !item.self)
			})
		},
		delTab(index) {
			this.residueTab.push(this.selfTab[index])
			this.selfTab.splice(index, 1)
		},
		addTab(index) {
			if (!this.isDisplay) return
			this.selfTab.push(this.residueTab[index])
			this.residueTab.splice(index, 1)
		},
		putTabList(arr) {
			let labels = []
			// 只需保存标签的_id即可
			arr.forEach(item => {
				labels.push(item._id)
			})
			uni.showLoading({
				title: "保存中..."
			})
			// 发送请求
			this.$api.putTabListC({
				labels
			}).then(res => {
				uni.hideLoading()
				uni.showToast({
					title: "编辑成功",
					icon: "none"
				})
				// 刷新首页标签
				uni.$emit('putTabList')
			}).catch(() => {
				uni.hideLoading()
				uni.showToast({
					title: "编辑失败",
					icon: "none"
				})
			})
		}
	}
}
